:root {
  --bg-color: #00acdc;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

.moyo {
  height: 40px;
  margin: 10px 50px;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding-right: 50px;
  display: flex;
}

.nav {
  justify-content: end;
  align-items: center;
  height: 60px;
  display: flex;
}

.navList {
  flex-wrap: nowrap;
  padding: 0;
  list-style: none;
  display: flex;
}

.navItem {
  justify-content: space-between;
  margin: 0 20px 0 0;
  padding: 0;
  position: relative;
}

.button {
  text-align: center;
  color: #000;
  cursor: pointer;
  height: 100%;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--bg-color);
  opacity: 1;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.button:hover, .is-active, .header .nav .navList .navItem .is-active {
  color: var(--bg-color);
}

.navItem:last-child {
  margin-right: 0;
}
